<template>
  <el-dialog title="OTA連携" :visible="dialogStatus">
    <el-form :model="data">
      <el-form-item label="施設" :label-width="formLabelWidth">
        <el-select v-model="data.facility_id" placeholder="選択してください">
          <el-option
            v-for="item in options.facilities"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="OTA" :label-width="formLabelWidth">
        <el-select v-model="data.provider" placeholder="選択してください">
          <el-option
            v-for="item in options.provider"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="OTA部屋" :label-width="formLabelWidth">
        <el-select v-model="data.ota_rooms" multiple placeholder="選択してください">
          <el-option
            v-for="item in options.ota_rooms"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Name" :label-width="formLabelWidth">
        <el-input v-model="data.name" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="アカウント" :label-width="formLabelWidth">
        <el-input v-model="data.account" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="パスワード" :label-width="formLabelWidth">
        <el-input v-model="data.password" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="状態" :label-width="formLabelWidth">
        <el-select v-model="data.status" placeholder="選択してください">
          <el-option
            v-for="item in options.status"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="toggleModal">キャンセル</el-button>
      <el-button type="primary" @click="Create()">新規</el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    props: {
      dialogStatus: Boolean,
      toggleModal: Function,
      init: Function
    },
    data() {
      return {
        data: {
          provider: '',
          account: '',
          password: '',
          name: '',
          facility_id: ''
        },
        options: {
          provider: [{
            value: 'rakuten',
            label: '楽天'
          }, {
            value: 'booking',
            label: 'Booking'
          }],
          facilities: [],
          ota_rooms: [],
          status: [{
            value: '0',
            label: '0'
          }, {
            value: '1',
            label: '1'
          }, {
            value: '2',
            label: '2'
          }]
        },
        formLabelWidth: '120px',
      }
    },
    methods: {
      initOption () {
        // 施設 data
        this.axios.get('/api/v1/facilities')
        .then(res => {
          this.options.facilities = res.data.facilities
        })
        .catch(error => {
          this.alert = { type: error.data.status, message: error.data.message }
        })
        // OTA部屋 data
        this.axios.get('/api/v1/ota_rooms')
        .then(res => {
          this.options.ota_rooms = res.data.ota_rooms
        })
        .catch(error => {
          this.alert = { type: error.data.status, message: error.data.message }
        })
      },
      Create () {
        this.axios.post('/api/v1/ota', {
          otum: this.data
        })
        .then(res => {
          // Clear data
          this.data = {}
          // Modal close
          this.toggleModal()
          // Alert
          this.$message({
            message: 'ok',
            type: res.data.status
          })
          // update
          this.init()
        })
        .catch(res => {
          // Modal close
          this.toggleModal()
          // Alert
          this.$message({
            message: res.data.message,
            type: res.data.status
          })
        })
      }
    },
    mounted () {
      this.initOption()
    }
  }
</script>